<script>
import axios from 'axios';

export default {
  name: 'AdvancedTables',
  data () {
    return {
      searched:'',
      showDialog: false,
      selected: [],
      data: [],
      searchIcon:null,
      totalData:'0',
      collapse: true,
      collapse2: false,
      bgColor:null,
      textColor:null,
      viewDetails:false,
      viewTable:true
    }
  },
  mounted(){
    axios.get(`../data.json`).then(response => {
      this.data = response.data.data_produktivitas.data
      this.searchIcon = 'search'
      this.totalData = response.data.data_produktivitas.total_data
    })

  
    
  },
  methods: {
    onSelect (items) {
      this.selected = items
    },
    getAlternateLabel (count) {
      let plural = ''

      if (count > 1) {
        plural = 's'
      }

      return `${count} user${plural} selected`
    },
    searchData(){
      // console.log(this.searched)

      // the code you're looking for
      var search = this.searched;

      const dataSearch = []
      for (var i = 0; i < this.data.length; i++){
        if (this.data[i].id == search || this.data[i].nolp == search || this.data[i].polres == search || this.data[i].tanggal_laka == search ){
          dataSearch.push(this.data[i]);
        }
      }


      this.data = dataSearch


      if(this.searchIcon == 'clear'){
        this.searchIcon = 'search'
        this.searched = null
        axios.get(`../data.json`).then(response => {
          this.data = response.data.data_produktivitas.data
        })
      } else {
        this.searchIcon = 'clear'
      }



     
    },
    openDialog(){
      // this.showDialog=true
      // this.$store.commit('toggleSidebarDesktop')
      this.viewTable = false
      this.viewDetails = true
    },
    backToList(){
      // this.showDialog=true
      // this.$store.commit('toggleSidebarDesktop')
      this.viewTable = true
      this.viewDetails = false
    },
    batalDialog(){
      this.showDialog=false
      this.$store.commit('toggleSidebarDesktop')
    }
  }
}
</script>